import { AgGridVue } from "ag-grid-vue3";
import axios from "axios";
export default {
  name: "DeliveryModal",
  components: {
    AgGridVue
  },
  props: {
    visible: {
      type: Boolean,
      required: true
    }
  },
  watch: {
    visible(newVal) {
      if (newVal) {
        this.deliverytList();
      }
    }
  },
  data() {
    return {
      rowData: [],
      columnDefs: [{
        field: "delivery_code",
        headerName: "출고번호",
        flex: 1
      }, {
        field: "delivery_name",
        headerName: "출고명",
        flex: 1
      }, {
        field: "delivery_date",
        headerName: "출고일자",
        flex: 1
      }, {
        field: "company_name",
        headerName: "업체명",
        flex: 1
      }, {
        field: "prod_name",
        headerName: "제품명",
        flex: 2
      }],
      gridOptions: {
        domLayout: "autoHeight",
        // singleClickEdit: true,               // 셀 편집 ON
        // suppressRowClickSelection: true,     // 행 클릭 선택 OFF
        suppressCellFocus: true,
        // 셀 포커스 OFF
        pagination: true,
        // 페이징 ON
        paginationPageSize: 5,
        // 한 페이지 보여질 행 수
        paginationPageSizeSelector: false,
        // paseSize 선택란 삭제
        defaultColDef: {
          // 열(컬럼) 기본 설정
          suppressMovable: true,
          // 컬럼을 드래그하여 이동하지 못하게
          resizable: false,
          // 컬럼 크기 조절 못하게
          sortable: false,
          // 정렬 못하게
          cellStyle: {
            textAlign: "center"
          } // 텍스트를 가운데 정렬
        }
      }
    };
  },
  methods: {
    close() {
      this.$emit("close");
    },
    async deliverytList() {
      await axios.get('/api/sales/deliveryModal').then(res => {
        this.rowData = res.data;
      });
    },
    onRowClicked(event) {
      this.$emit('selectOrder', event.data);
      this.close();
    }
  }
};